<template>
  <section class="sv-input-wrapper" :class="{'sv-item-input': isItem}">
    <div class="sv-input-left">
      <div
        class="sv-input-icon left-icon"
        v-if="inputIcon && inputIconPosition === 'left'">
        <slot name="icon">
          <i :class="inputLeftIconClass"></i>
          <!--<i class="sv-icon-search" v-if="inputType === 'search'"></i>-->
          <!--<i class="sv-icon-password" v-if="inputType === 'password'"></i>-->
        </slot>
      </div>
      <label class="sv-input-label" v-if="label">{{label}}</label>
    </div>

    <input
      class="sv-input"
      :class="inputClass"
      :inputType="type"
      :placeholder="placeholder"
      :value="inputValue"
      :maxlength="maxlength"
      v-model="inputValue"
      ref="svInputRef"
      @input="handleInput"
      @click="handleClick"
      :readonly="inputReadonly"/>

    <div class="sv-input-right">
      <div class="sv-input-icon right-icon" v-if="(inputIcon && inputIconPosition === 'right') || (inputType === 'password' && toggleVisible) || (clearable && inputValue)">
        <slot name="icon">
          <i class="sv-icon-clear" v-if="clearable && inputValue" @click="handleClearClick"></i>
          <i :class="{'sv-icon-password-hide': !passwordVisible, 'sv-icon-password-show': passwordVisible}" v-if="inputType === 'password' && toggleVisible" @click="togglePasswordVisible"></i>
          <i :class="inputRightIconClass" v-else></i>
        </slot>
      </div>
      <div class="sv-input-unit" v-if="unit">
        <div class="unit-text">{{unit}}</div>
      </div>
    </div>
  </section>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'svInput',
    data () {
      return {
        inputValue: this.value,
        inputType: this.type,
        inputIcon: this.icon,
        inputIconPosition: this.iconPosition,
        inputReadonly: this.readonly,
        passwordVisible: false
      }
    },
    props: {
      value: [String, Number],
      icon: {
        type: String,
        default: ''
      },
      iconPosition: {
        type: String,
        default () {
          return 'left'
        }
      },
      size: {
        type: String,
        default: ''
      },
      type: {
        type: String,
        default: 'text'
      },
      placeholder: {
        type: String,
        default: ''
      },
      readonly: {
        type: Boolean,
        default: false
      },
      disabled: {
        type: Boolean,
        default: false
      },
      toggleVisible: {
        type: Boolean,
        default: true
      },
      maxlength: {
        type: [String, Number]
      },
      unit: {
        type: String,
        default: ''
      },
      clearable: {
        type: Boolean,
        default: false
      },
      isItem: {
        type: Boolean,
        default: false
      },
      label: String
    },
    watch: {
      inputValue (val) {
        this.setInputValue(val)
      },
      value (val) {
        this.setInputValue(val)
      }
    },
    computed: {
      inputClass () {
        let inputClass = ''
        if (this.size) {
          inputClass += `sv-input-${this.size}`
        }
        if (this.disabled) {
          inputClass += ` disabled`
        }
        return inputClass
      },
      inputLeftIconClass () {
        if (this.iconPosition === 'left') {
          return this.inputIcon ? `sv-icon-${this.inputIcon}` : ''
        }
        return ''
      },
      inputRightIconClass () {
        if (this.iconPosition === 'right') {
          return this.inputIcon ? `sv-icon-${this.inputIcon}` : ''
        }
        return ''
      }
    },
    methods: {
      handleInput (event) {
        let value = event.target.value
        this.setInputValue(value)
      },
      setInputValue (value) {
        value = value + '' // 将数字类型转换为字符串类型
        if (this.type === 'integer') {
          let newValue = value
          value = value.replace(/\D+/g, '')
          if (newValue.startsWith('-')) {
            value = '-' + value
          }
        }

        if (this.type === 'number') {
          if (typeof this.enableNegative === 'string') {
            if (this.enableNegative === '' || this.enableNegative === 'true') {
              this._enableNegative = true
            } else {
              this._enableNegative = false
            }
          }

          if (typeof this.enableNegative === 'boolean') {
            this._enableNegative = this.enableNegative
          }

          if (this._enableNegative) {
            let startValue = ''
            if (value.startsWith('-')) {
              startValue = '-'
              if (value.charAt(1) === '.') {
                startValue = '-'
              }
            }

            value = value.replace(/[^\d.]/g, '')

            if (startValue === '-') {
              if (value === '.') {
                value = ''
              }
            }
            value = startValue + value
          } else {
            value = value.replace(/[^\d.]/g, '')
          }

          let values = value.split('.')

          if (values.length === 2) {
            if (!values[0]) {
              value = ''
            } else {
              let newValue = ''
              newValue = values[0] + '.' + (values[1] ? values[1].substring(0, 2) : '')
              value = newValue
            }
          }

          if (values.length > 2) {
            values.pop()
            let newValue = ''
            for (let i = 0; i < values.length; i++) {
              newValue += i === values.length - 1 ? values[i] : values[i] + '.'
            }
            value = newValue
          }
        }

        if (this.type === 'hour') {
          value = value.replace(/\D+/g, '')
          if (value.charAt(0) === '0') {
            value = value.charAt(0)
          }
          if (window.parseInt(value) > 23) {
            value = value.substr(0, value.length - 1)
          }
        }
        this.inputValue = value
        this.$emit('input', value)
      },
      setInputType (type) {
        this.$nextTick(() => {
          this.$refs.svInputRef.setAttribute('type', type)
        })
      },
      togglePasswordVisible () {
        this.passwordVisible = !this.passwordVisible
        this.setInputType(this.passwordVisible ? 'text' : 'password')
      },
      handleClick (value) {
        this.$emit('click', value)
      },
      handleClearClick () {
        this.inputValue = ''
        this.$emit('input', '')
        this.$emit('clear')
      }
    },
    created () {
      this.setInputType(this.inputType)
      if (this.disabled) {
        this.inputReadonly = true
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";
  @import "../../icon-fonts/style.css";
  .sv-input-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width:100%;
    height: pxToRem(80);
    overflow: hidden;
    border: 1px solid #ddd;
    border-radius: pxToRem(4);
    font-size: pxToRem(26);
    &.sv-item-input{
      border: none;
      border-bottom: 1px solid #ddd;
      .sv-input-left{
        .sv-input-label{
          font-size: pxToRem(30);
          color:#333;
        }
      }
    }
    .sv-input-icon{
      padding: pxToRem(0 14);
      height: pxToRem(60px);
      line-height: pxToRem(60px);
      font-size: pxToRem(40px);
      text-align: center;
      color:#bbb;
      &.left-icon{
        /*border-right: 1px solid #ddd;*/
      }
      &.right-icon{
        /*border-left: 1px solid #ddd;*/
      }
    }
    .sv-input{
      flex: 1;
      padding: pxToRem(0 20px);
      color:#666;
      border: none;
      width: 100%;
      height: 100%;
      &.disabled{
        background: #f5f7fa;
        color:#c0c4cc;
        &::-webkit-input-placeholder{
          color: #c0c4cc;
        }
      }
    }
    .sv-input-left, .sv-input-right{
      display: flex;
      align-items: center;
      height: 100%;
    }
    .sv-input-right{
      .sv-input-unit{
        display: flex;
        align-items: center;
        height: 100%;
        padding:pxToRem(0 14);
        box-sizing: border-box;
        border-left: 1px solid #bfcbd9;
        background-color: #fbfdff;
        color: #97a8be;
        font-style: normal;
        font-size: pxToRem(14);
        .unit-text{
          max-width: pxToRem(100);
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }

</style>



