<template>
  <div class="sv-radio-group">
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'svRadioGroup',
    props: {
      value: [String, Number, Boolean],
      type: {
        type: String
      }
    },
    data () {
      return {
        radioGroupValue: this.value
      }
    },
    watch: {
      value (val) {
        this.radioGroupValue = val
      },
      radioGroupValue (val) {
        this.setValueToChildren()
        this.$emit('input', val)
      }
    },
    methods: {
      setValueToChildren () {
        this.$children.forEach(child => {
          child.radioGroupValue = this.radioGroupValue
        })
      }
    },
    mounted () {
      this.setValueToChildren()
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";
  .sv-radio-group{
    display: inline-block;
    font-size: 0;
    .sv-radio{
      &:not(:last-child){
        margin-right: pxToRem(30);
      }
    }
  }
</style>



