<template>
  <sv-layout>
    <sv-main>
      <sv-slide-box>
        <sv-slide-item>
          <div class="download-wrapper">
            <hgroup class="hgroup">
              <h2 class="title">优挂司机</h2>
              <h3 class="subtitle">安全送货 轻松赚钱</h3>
            </hgroup>
            <footer class="footer">
              <div class="button-group">
                <button class="button-download" @click="download(0)">
                  <img class="icon" :src="iphoneIcon"/>
                  <span class="text">下载iPhone客户端</span>
                </button>
                <button class="button-download" @click="download(1)">
                  <img class="icon" :src="androidIcon" />
                  <span class="text">下载Android客户端</span>
                </button>
              </div>
              <div class="button-next">
                <img :src="arrowDownIcon" class="image"/>
              </div>
            </footer>
          </div>
        </sv-slide-item>
        <sv-slide-item>
          <div class="download-carousel-wrapper">
            <div class="phone-box-image-wrapper">
              <img :src="phoneBoxImage" class="phone-box-image"/>
            </div>
            <div class="title-wrapper">
              <ul class="title-list" ref="titleListRef">
                <li class="title-item" v-for="item in carouselItems">{{item.title}}</li>
              </ul>
            </div>
            <div class="carousel">
              <sv-carousel
                height="100%"
                :loop="false"
                :show-indicator="false"
                @slide-start="handleTouchStart"
                @slide-move="handleTouchMove"
                @slide-end="handleTouchEnd"
                ref="carouselRef">
                <sv-carousel-item v-for="item in carouselItems">
                  <img :src="item.image" class="image">
                </sv-carousel-item>
              </sv-carousel>
            </div>
            <footer class="footer">
              @北京中交信源科技有限公司
            </footer>

            <button class="sv-carousel-control sv-carousel-control-prev" :class="{'disabled': currentIndex === 0}" @click="prev">
              <i class="arrow-icon"></i>
            </button>
            <button class="sv-carousel-control sv-carousel-control-next" :class="{'disabled': currentIndex === carouselItems.length - 1}" @click="next">
              <i class="arrow-icon"></i>
            </button>
          </div>
        </sv-slide-item>
      </sv-slide-box>
    </sv-main>
  </sv-layout>
</template>

<script>
import '@/commons/base/computedRootFontSize.js'

import iphoneIcon from '../../assets/download/icon-iphone.png'
import androidIcon from '../../assets/download/icon-android.png'

import arrowDownIcon from '../../assets/download/arrow-down.png'

import downloadCarouselImage0 from '../../assets/download/driver/0.png'
import downloadCarouselImage1 from '../../assets/download/driver/1.png'
import downloadCarouselImage2 from '../../assets/download/driver/2.png'
import downloadCarouselImage3 from '../../assets/download/driver/3.png'

import phoneBoxImage from '../../assets/download/phone-box-image.png'

import axios from 'axios'
export default {
  name: 'app',
  data () {
    return {
      iphoneIcon,
      androidIcon,
      arrowDownIcon,
      phoneBoxImage,
      carouselItems: [
        {
          image: downloadCarouselImage0,
          title: '随时上报车辆行车动态'
        },
        {
          image: downloadCarouselImage1,
          title: '接货接单更加便捷'
        },
        {
          image: downloadCarouselImage2,
          title: '不花一分钱 免费找货源'
        },
        {
          image: downloadCarouselImage3,
          title: '实时播报 语音提醒'
        }
      ],
      carouselWidth: 0,
      loop: false,
      touchHandler: {
        startX: 0,
        startY: 0,
        currentX: 0,
        currentY: 0,
        endX: 0,
        endY: 0,
        translateX: 0,
        threshold: 100, // 阈值
        startTime: 0
      },
      direction: 'left', // 轮播的方向 left/right
      currentIndex: 0,
      carouselItemCount: 4
    }
  },
  methods: {
    handleTouchStart (event) {
      this.touchHandler.startX = event.touches[0].pageX
      this.touchHandler.startY = event.touches[0].pageY
      this.touchHandler.startTime = Date.now()
    },
    handleTouchMove (event) {
      let currentX = event.touches[0].pageX
      let currentY = event.touches[0].pageY
      let currentTime = new Date().getTime()
      let {startX} = this.touchHandler

      this.direction = this.getDirection(currentX, currentY)
      if (this.direction === 'up' || this.direction === 'down') {
        return
      }

      this.direction = currentX < this.touchHandler.startX ? 'left' : 'right'
      if (this.direction === 'left') { // 向左滑动
        if (this.loop) {
          if (this.currentIndex <= this.carouselItemCount) {
            // 计算当前激活的图片index
            this.currentIndex = Math.floor(Math.abs(this.touchHandler.translateX) / this.carouselWidth) - 1
            this.touchHandler.translateX += (currentX - startX) / 10
          } else {
            this.currentIndex = this.carouselItemCount - 1
            this.touchHandler.translateX += (currentX - startX) / ((currentTime - this.touchHandler.startTime) / 10)
          }
        } else {
          if (this.currentIndex <= this.carouselItemCount) {
            // 计算当前激活的图片index
            this.currentIndex = Math.floor(Math.abs(this.touchHandler.translateX) / this.carouselWidth)
            this.touchHandler.translateX += (currentX - startX) / 10
          } else {
            this.currentIndex = this.carouselItemCount - 1
            this.touchHandler.translateX += (currentX - startX) / ((currentTime - this.touchHandler.startTime) / 10)
          }
        }
      } else { // 向右滑动
        if (this.loop) {
          this.touchHandler.translateX += (currentX - startX) / 10
          if (this.touchHandler.translateX >= 0) {
            this.currentIndex = this.carouselItemCount - 1
            this.translateX = -this.currentIndex * this.carouselWidth
          }
        } else {
          if (this.touchHandler.translateX < 0) {
            // 计算当前激活的图片index
            this.currentIndex = Math.ceil(Math.abs(this.touchHandler.translateX) / this.carouselWidth)
            this.touchHandler.translateX += (currentX - startX) / 10
          } else {
            this.currentIndex = 0
            this.touchHandler.translateX += (currentX - startX) / ((currentTime - this.touchHandler.startTime) / 10)
          }
        }
      }

      this.slide(this.touchHandler.translateX)
    },
    handleTouchEnd (event) {
      let touch = event.changedTouches[0]
      let endX = touch.pageX
      let endY = touch.pageY
      let slideX = endX - this.touchHandler.startX
      let duration = 0.3

      this.direction = this.getDirection(endX, endY)
      if (this.direction === 'up' || this.direction === 'down') {
        return
      }

      if (Math.abs(slideX) > this.touchHandler.threshold) {
        if (this.direction === 'left') { // 向左滑动
          this.currentIndex = ++this.currentIndex

          if (this.loop) {
            if (this.currentIndex === this.carouselItemCount) {
              let loopTimeout = window.setTimeout(() => {
                this.currentIndex = 0
                this.touchHandler.translateX = -this.carouselWidth
                this.slide(this.touchHandler.translateX, 0)
                window.clearTimeout(loopTimeout)
              }, duration * 1000)
            }
          } else {
            (this.currentIndex > this.carouselItemCount - 1) && (this.currentIndex = this.carouselItemCount - 1)
          }
        } else { // 向右滑动
          this.currentIndex = --this.currentIndex
          if (this.loop) {
            if (this.currentIndex === -1) {
              let loopTimeout = window.setTimeout(() => {
                this.currentIndex = this.carouselItemCount
                this.touchHandler.translateX = -(this.currentIndex + 1) * this.carouselWidth
                this.slide(this.touchHandler.translateX, 0)
                window.clearTimeout(loopTimeout)
              }, duration * 1000)
            }
          } else {
            (this.currentIndex < 0) && (this.currentIndex = 0)
          }
        }
      }

      if (this.loop) {
        this.touchHandler.translateX = -(this.currentIndex + 1) * this.carouselWidth
      } else {
        this.touchHandler.translateX = -this.currentIndex * this.carouselWidth
      }

      this.slide(this.touchHandler.translateX, duration)
      this.autoplay && this.autoPlay()
    },
    slide (distance, duration = 0.2, timing = 'linear') {
      this.$refs.titleListRef.style.transform = `translate3d(${distance}px, 0, 0)`
      this.$refs.titleListRef.style.transition = duration > 0 ? `transform ${duration}s ${timing}` : ''
    },
    getDirection (targetX, targetY) {
      let direction = ''
      let {startX, startY} = this.touchHandler
      let [hDistance, vDistance] = [Math.abs(targetX - startX), Math.abs(targetY - startY)]
      if (targetX < startX) { // 1，3象限
        if (targetY < startY) { // 1象限
          if (hDistance < vDistance) { // up
            direction = 'up'
          } else { // left
            direction = 'left'
          }
        } else { // 3象限
          if (hDistance < vDistance) { // down
            direction = 'down'
          } else { // left
            direction = 'left'
          }
        }
      } else { // 2, 4象限
        if (targetY < startY) { // 2象限
          if (hDistance < vDistance) { // up
            direction = 'up'
          } else { // right
            direction = 'right'
          }
        } else { // 4象限
          if (hDistance < vDistance) { // down
            direction = 'down'
          } else { // right
            direction = 'right'
          }
        }
      }
      return direction
    },
    prev () {
      this.$refs.carouselRef.prev()
      if (this.currentIndex <= 0) {
        return
      }
      this.currentIndex--
      this.touchHandler.translateX = -this.currentIndex * this.carouselWidth
      this.slide(this.touchHandler.translateX, 0.4)
    },
    next () {
      this.$refs.carouselRef.next()
      if (this.currentIndex >= this.carouselItemCount - 1) {
        return
      }
      this.currentIndex++
      this.touchHandler.translateX = -this.currentIndex * this.carouselWidth
      this.slide(this.touchHandler.translateX, 0.4)
    },
    download (index) {
      if (index === 0) { // ios
        if (!this.isIOS()) {
          this.$toast('android操作系统请下载Android客户端')
          return
        }
        window.location.href = 'https://itunes.apple.com/us/app/%E4%BC%98%E6%8C%82%E5%8F%B8%E6%9C%BA/id1261835266?l=zh&ls=1&mt=8'
      } else { // android
        if (this.isIOS()) {
          this.$toast('IOS操作系统请下载iPhone客户端')
          return
        }
        let winRef = window.open('', '_blank')
        this.getAndroidDownloadURL(url => {
          winRef.location.href = url
        })
      }
    },
    getAndroidDownloadURL (callback) {
      let backupURL = 'https://zjhyl.oss-cn-hangzhou.aliyuncs.com/app/consignor/online/6283032956723328619.0.0.0_2018-06-20_zjxl_channel.apk'
      axios.get('https://api.utrailer.cn/utrailer-version/check', {
        params: {
          osType: 'android',
          productType: 'DRIVER',
          version: 2000
        }
      }).then(response => {
        if (response.data.updateUrl) {
          callback(response.data.updateUrl)
        } else {
          callback(backupURL)
        }
      })
    },
    isIOS () {
      let ua = window.navigator.userAgent.toLowerCase()
      return /(iPhone|iPad|iPod|iOS)/i.test(ua)
    }
  },
  created () {
    this.$nextTick(() => {
      let carouselRect = this.$el.getBoundingClientRect()
      this.carouselWidth = carouselRect.width
    })
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../commons/base/base";
  .download-wrapper{
    position: relative;
    width: 100%;
    height: 100%;
    background: url('../../assets/download/driver/bg.png') no-repeat;
    background-size: 100% 100%;
    .hgroup{
      padding: pxToRem(38 0 0 102);
      color:$color-light;
      font-size: pxToRem(60);
      .title{
        margin-bottom: pxToRem(40);
        font-weight: bold;
      }
      .subtitle{
      }
    }
    .footer{
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: pxToRem(40);
      .button-group{
        .button-download{
          display: flex;
          justify-content: center;
          align-items: center;
          width: pxToRem(400);
          height: pxToRem(88);
          border:none;
          outline:none;
          border-radius: pxToRem(10);
          color:$color-light;
          font-size: pxToRem(30);
          background: linear-gradient(to right, #f79415, #f17f17);
          white-space: nowrap;
          cursor: pointer;
          .icon{
            width: pxToRem(30);
            height: pxToRem(40);
            margin-right: pxToRem(16);
          }
          &:first-child{
            margin-bottom: pxToRem(60);
          }
        }
      }
      .button-next{
        display: flex;
        justify-content: center;
        margin-top: pxToRem(20);
        text-align: center;
        .image{
          width: pxToRem(40);
          height: pxToRem(26);
          animation: loop 1.5s infinite;
        }
        @keyframes loop {
          from {
            transform: translateY(0) scale(1);
          }
          to {
            transform: translateY(10px) scale(1.2);
          }
        }
      }
    }
  }
  .download-carousel-wrapper{
    width: 100%;
    height: 100%;
    background: #f2f2f2;
    position: relative;
    .phone-box-image-wrapper{
      position: absolute;
      top: pxToRem(160);
      left: 50%;
      z-index: 1;
      transform: translateX(-50%);
      width: pxToRem(500);
      height: pxToRem(920);
      .phone-box-image{
        width: 100%;
        height: 100%;
      }
    }
    .title-wrapper{
      padding-top: pxToRem(60);
      font-size: pxToRem(48);
      font-weight: bold;
      color:#333;
      text-align: center;
      overflow: hidden;
      .title-list{
        display: flex;
        flex-wrap: nowrap;
        .title-item{
          width: 100%;
          flex-shrink: 0;
          text-align: center;
        }
      }
    }
    .carousel{
      position: absolute;
      top: pxToRem(286);
      left: 50%;
      z-index:2;
      transform: translateX(-50%);
      width: pxToRem(434);
      height: pxToRem(794);
      .image{
        width: 100%;
        height: 100%;
      }
    }
    .sv-carousel-control{
      position: absolute;
      top: 50%;
      z-index: 999;
      transform: translateY(-50%);
      display: flex;
      justify-content: center;
      align-items: center;
      width: pxToRem(60);
      height: pxToRem(60);
      border: 1px solid #989898;
      border-radius: 50%;
      outline:none;
      background: transparent;
      &.disabled{
        border-color: #cdcdcd;
        .arrow-icon{
          border-left-color: #ccc;
          border-bottom-color: #ccc;
        }
      }
      .arrow-icon{
        display: inline-block;
        width: pxToRem(20);
        height: pxToRem(20);
        border-left: 2px solid #999;
        border-bottom: 2px solid #999;
      }
      &.sv-carousel-control-prev{
        left: pxToRem(20);
        .arrow-icon{
          transform: rotate(45deg);
        }
      }
      &.sv-carousel-control-next{
        right: pxToRem(20);
        .arrow-icon{
          transform: rotate(-135deg);
        }
      }
    }
    .footer{
      position: absolute;
      bottom: 0;
      width: 100%;
      height: pxToRem(70);
      line-height: pxToRem(70);
      text-align: center;
      background: $color-light;
      color:#999;
      font-size: pxToRem(30);
    }
  }
</style>




