<template>
 <main class="sv-main" :class="{'has-header': hasHeader, 'has-subheader': hasSubheader, 'has-footer': hasFooter}">
   <slot></slot>
 </main>
</template>

<script>
  export default {
    name: 'svMain',
    props: {
      hasHeader: {
        type: [String, Boolean],
        default: false
      },
      hasSubheader: {
        type: [String, Boolean],
        default: false
      },
      hasFooter: {
        type: [String, Boolean],
        default: false
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../../styles/base";

  .sv-main{
    position:absolute;
    left: 50%;
    top: 0;
    bottom: 0;
    width: 100%;
    max-width: 750px;
    min-width: 320px;
    overflow-x: hidden;
    overflow-y: scroll;
    transform: translateX(-50%);
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar{display: none;}
    &.has-header{
      top: pxToRem(100px);
    }
    &.has-subheader{
      top: pxToRem(200);
    }
    &.has-footer{
      bottom: pxToRem(100px);
    }
  }
</style>



