<template>
  <section class="sv-tab">
    <slot></slot>
    <slot name="icon"></slot>
    <slot name="title"></slot>
  </section>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'svTab',
    props: {
      value: {
        type: [String, Number]
      },
      title: {
        type: String
      },
      icon: String
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss"></style>



