<template>
  <section class="sv-nodata-wrapper">
    <div class="sv-nodata-icon" :class="{'refreshing': refreshing}" @click="handleClick">
      <i class="icon" :class="icon"></i>
    </div>
    <div class="sv-nodata-text" :class="{'refreshing': refreshing}">
      <span v-if="refreshing">正在刷新...</span>
      <span v-else>点击刷新一下吧</span>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'svNodata',
    data () {
      return {
        icon: 'icon-no-data',
        refreshing: false // 是否正在刷新
      }
    },
    props: {
      refreshCompleted: {
        type: Boolean,
        default: false
      }
    },
    watch: {
      refreshCompleted (val) {
        if (val) {
          this.refreshing = false
          this.icon = 'icon-no-data'
        }
      }
    },
    methods: {
      handleClick (event) {
        if (this.refreshing) return
        this.icon = 'icon-refresh'
        this.$emit('click', event)
        this.refreshing = true
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";
  @import "icon-fonts/style.css";

  .sv-nodata-wrapper{
    position: fixed;
    top: 30%;
    left:50%;
    transform: translate(-50%, -50%);
    .sv-nodata-icon{
      display: flex;
      justify-content: center;
      align-items: center;
      width: pxToRem(170px);
      height: pxToRem(170px);
      margin: 0 auto;
      border-radius: 50%;
      background: #e7e7e7;
      &.refreshing{
        &:active{
          background: #e7e7e7;
        }
      }
      &:active{
        background: #e1e1e1;
      }
      .icon{
        &.icon-no-data{
          color:#cfcfcf;
          font-size: pxToRem(100px);
        }
        &.icon-refresh{
          width:pxToRem(60px);
          height:pxToRem(60px);
          border: pxToRem(8px) solid $color-danger;
          border-top-color: $color-light;
          border-radius: pxToRem(30px);
          color:#ff7800;
          font-size: pxToRem(100px);
          animation: rotate 2s infinite linear;
          @keyframes rotate {
            from{
              transform:rotate(0);
            }
            to{
              transform: rotate(360deg);
            }
          }
        }
      }
    }
    .sv-nodata-text{
      margin-top: pxToRem(32px);
      text-align: center;
      font-size: pxToRem(28px);
      color: #999;
      &.refreshing{
        color: $color-danger;
      }
    }
  }
</style>



