<template>
    <div class="sv-timeline-item">
      <template v-if="timePosition === 'left'">
        <div class="sv-timeline-item-time" :style="timeStyle.left">
          <slot name="time"></slot>
        </div>

        <div class="sv-timeline-item-dot-and-line">
          <div class="sv-timeline-item-dot">
            <slot name="dot">
              <i class="dot-entry" :class="dotClass"></i>
            </slot>
          </div>
          <div class="sv-timeline-item-line" v-if="hasLine || (typeof(hasLine) === 'string' && hasLine === '')">
            <p class="line-entry" :class="lineClass"></p>
          </div>
        </div>

        <div class="sv-timeline-item-content">
          <div class="sv-timeline-item-content-body">
            <slot></slot>
          </div>
          <div class="sv-timeline-item-content-footer">
            <slot name="note"></slot>
          </div>
        </div>
      </template>
      <template v-else>
        <div class="sv-timeline-item-dot-and-line">
          <div class="sv-timeline-item-dot">
            <slot name="dot">
              <i class="dot-entry" :class="dotClass"></i>
            </slot>
          </div>
          <div class="sv-timeline-item-line" v-if="hasLine || (typeof(hasLine) === 'string' && hasLine === '')">
            <p class="line-entry" :class="lineClass"></p>
          </div>
        </div>

        <div class="sv-timeline-item-main">
          <div class="sv-timeline-item-time" :style="timeStyle.right">
            <slot name="time"></slot>
          </div>

          <div class="sv-timeline-item-content">
            <div class="sv-timeline-item-content-body">
              <slot></slot>
            </div>
            <div class="sv-timeline-item-content-footer">
              <slot name="note"></slot>
            </div>
          </div>
        </div>
      </template>
    </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'svTimelineItem',
    props: {
      dotType: String,
      dotColor: String,
      dotSize: String,
      lineType: String,
      lineColor: String
    },
    data () {
      return {
        timePosition: '',
        hasLine: true,
        computeDotType: this.dotType,
        computeDotColor: this.dotColor,
        computeDotSize: this.dotSize,
        computeLineType: this.lineType,
        computeLineColor: this.lineColor,
        timeStyle: {
          left: {
            display: 'block'
          },
          right: {
            display: 'block'
          }
        }
      }
    },
    computed: {
      dotClass () {
        return `dot-type-${this.computeDotType} dot-color-${this.computeDotColor} dot-size-${this.computeDotSize}`
      },
      lineClass () {
        return `line-type-${this.computeLineType} line-color-${this.computeLineColor}`
      }
    },
    methods: {
      setFieldByParentField () {
        let parent = this.$parent
        this.timePosition = parent.timePosition
        this.hasLine = parent.hasLine
        this.computeDotType = this.dotType || parent.dotType
        this.computeDotColor = this.dotColor || parent.dotColor
        this.computeDotSize = this.dotSize || parent.dotSize
        this.computeLineType = this.lineType || parent.lineType
        this.computeLineColor = this.lineColor || parent.lineColor
      },
      setParentStyles () {
        let parent = this.$parent

        if (parent.timePosition === 'right') {
          this.timeStyle.right.display = this.$slots.time ? 'block' : 'none'
        }

        if (parent.timePosition === 'left') {
          this.$on('checkHasLeftTime', (hasLeftTime) => {
            this.timeStyle.left.display = hasLeftTime ? 'block' : 'none'
          })
        }
      }
    },
    mounted () {
      this.setFieldByParentField()
      this.setParentStyles()
    }
  }
</script>



