<template>
  <i class="sv-icon" :class="[iconClass, sizeClass, colorClass]"></i>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'svIcon',
    props: {
      icon: String,
      size: {
        type: String,
        default: 'default'
      },
      color: String
    },
    computed: {
      iconClass () {
        return `sv-icon-${this.icon}`
      },
      sizeClass () {
        return `sv-icon-${this.size}`
      },
      colorClass () {
        return this.color ? `sv-icon-${this.color}` : ''
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";
  .sv-icon{
    color:#333;
    &.sv-icon-small{
      font-size: pxToRem(26);
    }
    &.sv-icon-default{
      font-size: pxToRem(36);
    }
    &.sv-icon-large{
      font-size: pxToRem(46);
    }
    &.sv-icon-stable{
      color:$color-stable;
    }
    &.sv-icon-info{
      color:$color-info;
    }
    &.sv-icon-primary{
      color:$color-primary;
    }
    &.sv-icon-warning{
      color:$color-warning;
    }
    &.sv-icon-success{
      color:$color-success;
    }
    &.sv-icon-danger{
      color:$color-danger;
    }
    &.sv-icon-royal{
      color:$color-royal;
    }
    &.sv-icon-lively{
      color:$color-lively;
    }
  }
</style>



