import Mask from './Mask.vue'

export default {
  install (Vue) {
    Vue.component('svMask', Mask)

    let MaskConstructor = Vue.extend(Mask)
    let mask
    if (!this.$mask) {
      mask = new MaskConstructor().$mount(document.createElement('div'))
      Vue.prototype.$mask = mask
    }
    document.body.appendChild(mask.$el)
  }
}



