// 这个难道是一个es6的补丁？正常的vue-cli中已经包含了补丁了应该。所以这个暂时不知道去掉后怎么重现故障
import 'core-js/fn/array/find-index'

import Icon from './components/icon/Icon'
import Grid from './components/grid/Grid'
import GridCell from './components/grid/GridCell'

import Layout from './components/layout/Layout'
import Header from './components/layout/header/Header'
import Main from './components/layout/main/Main'
import Footer from './components/layout/footer/Footer'

import Button from './components/button/Button'
import ButtonGroup from './components/button-group/ButtonGroup'
import PaletteButton from './components/palette-button/PaletteButton'
import Img from './components/img/Img'
import Previewer from './components/previewer/Previewer.vue'
import Carousel from './components/carousel/Carousel'
import CarouselItem from './components/carousel/CarouselItem.vue'
import SlideBox from './components/slide-box/SlideBox.vue'
import SlideItem from './components/slide-box/SlideItem.vue'
import Loadmore from './components/loadmore/Loadmore'
import Route from './components/route/Route'
import Timeline from './components/timeline/Timeline'
import TimelineItem from './components/timeline/TimelineItem'
import Nodata from './components/no-data/Nodata'
import Tabs from './components/tabs/Tabs'
import Tab from './components/tabs/Tab'

import Table from './components/table/Table'
import TableColumn from './components/table/TableColumn'
import Input from './components/input/Input'
import List from './components/list/List'
import Item from './components/list/Item'
import Countdown from './components/countdown/Countdown'
import Rating from './components/rating/Rating'
import Badge from './components/badge/Badge'
import Radio from './components/radio/Radio.vue'
import RadioGroup from './components/radio/RadioGroup.vue'

import Picker from './components/picker/Picker'
import DistPicker from './components/picker/dist-picker/DistPicker'
import DatetimePicker from './components/picker/datetime-picker/DatetimePicker'

import Mask from './components/mask/Mask.vue'
import MaskPlugin from './components/mask'

import Dialog from './components/dialog'
import Toast from './components/toast'
import Modal from './components/modal/Modal'
import Popup from './components/popup/Popup'
/* Navigation */
import IndexList from './components/index-list/IndexList.vue'
import IndexItem from './components/index-list/IndexItem.vue'
/* Notice */
let SinoiovUIComponentsLib = {
  Grid,
  GridCell,
  Layout,
  Header,
  Main,
  Footer,
  Mask,
  Button,
  ButtonGroup,
  PaletteButton,
  Img,
  Previewer,
  Carousel,
  CarouselItem,
  SlideBox,
  SlideItem,
  Loadmore,
  Route,
  Timeline,
  TimelineItem,
  Nodata,
  Tabs,
  Tab,
  Icon,
  Table,
  TableColumn,
  Input,
  List,
  Item,
  Countdown,
  Rating,
  Badge,
  Radio,
  RadioGroup,
  Modal,
  Popup,
  Picker,
  DistPicker,
  DatetimePicker,
  IndexList,
  IndexItem
}

import VueTouch from './plugins/vue-touch'

const install = function (Vue, opts = {}) {
  Object.keys(SinoiovUIComponentsLib).forEach((key) => {
    Vue.component(SinoiovUIComponentsLib[key].name, SinoiovUIComponentsLib[key])
  })

  Vue.use(Dialog)
  // Vue.use(Loading)
  Vue.use(Toast)
  Vue.use(MaskPlugin)

  Vue.use(VueTouch)
}

// auto install
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}
// 把 install方法添加到 VueComponentsLib 中
export default Object.assign(SinoiovUIComponentsLib, {install})
// module.exports = Object.assign(tlzVueElUi, {install})   // eslint-disable-line no-undef



