<template>
    <sv-popup v-model="visible" position="bottom">
      <div class="sv-picker" v-if="visible">
        <div class="sv-picker-header">
          <slot name="header">
            <sv-button type="text" color="stable" @click="cancel">取消</sv-button>
            <sv-button type="text" color="primary" @click="confirm">确定</sv-button>
          </slot>
        </div>
        <section class="sv-picker-body" ref="svPickerBodyRef">
          <div class="sv-picker-handle"></div>
          <div class="sv-picker-group">
            <ul class="sv-picker-list" v-for="(slot, $listIndex) in slots">
              <li class="sv-picker-item"
                  :class="{'active': $itemIndex === pickerListHandlers[$listIndex].activeIndex}"
                  :style="getPickerItemStyle($listIndex, $itemIndex)"
                  v-for="(textOrValue, $itemIndex) in slot.texts && slot.texts.length > 0 ? slot.texts : slot.values">
                {{textOrValue}}
              </li>
            </ul>
          </div>
          <!--<slot></slot>-->
        </section>
      </div>
    </sv-popup>
  <!--
  <div v-else>
    <div class="sv-picker">
      <section class="sv-picker-body" ref="svPickerBodyRef">
        <slot></slot>
      </section>
    </div>
  </div>
  -->
</template>

<script type="es6">
  import svPopup from '../popup/Popup'
  export default {
    name: 'svPicker',
    components: {
      svPopup
    },
    data () {
      return {
        visible: false,
        computeSlots: this.slots,
        pickerItemHeight: 0,
        pickerListHandlers: []
      }
    },
    props: {
      isPopup: {
        type: Boolean,
        default: false
      },
      slots: {
        type: Array,
        default () {
          return []
        }
      }
    },
    methods: {
      initData () {
        for (let i = 0; i < this.slots.length; i++) {
          this.pickerListHandlers.push({
            activeIndex: 0,
            scrollHeight: 0,
            startY: 0,
            endY: 0,
            currentY: 0,
            initTranslateY: 0,
            translateY: 0
          })
        }
      },
      getPickerItemStyle (listIndex, itemIndex) {
        let activeIndex = this.pickerListHandlers[listIndex].activeIndex

        let [skewBaseAngle, rotateBaseAngle] = [10, 14]
        let offsetCount = itemIndex - activeIndex
        /*
         if (offsetCount === 1) {
         return {
         transform: `skewX(10deg) rotateX(-10deg)`,
         opacity: .8
         }
         }
         if (offsetCount === 2) {
         return {
         transform: `skewX(20deg) rotateX(-30deg)`,
         opacity: .6
         }
         }
         if (offsetCount === 3) {
         return {
         transform: `skewX(30deg) rotateX(-50deg)`,
         opacity: .4
         }
         }
         */
        if (offsetCount !== 0) {
          return {
            transform: `skewX(${skewBaseAngle * offsetCount}deg) rotateX(-${rotateBaseAngle * (2 * Math.abs(offsetCount) - 1)}deg)`,
            opacity: 1 - Math.abs(offsetCount) * 0.2
          }
        }
      },
      computeSomeValue () {
        this.pickerItemHeight = document.querySelector('.sv-picker-item').clientHeight
      },
      bindEvents () {
        let $pickerList = document.querySelectorAll('.sv-picker-list')
        let initTranslateY = 0
        let currentPickerList = null
        $pickerList.forEach(($picker, index) => {
          currentPickerList = this.pickerListHandlers[index]

          initTranslateY = this.pickerItemHeight * 3
          currentPickerList.initTranslateY = currentPickerList.translateY = initTranslateY
          this.translateY($picker, initTranslateY)

          $picker.addEventListener('touchstart', event => {
            this.handleTouchStart($picker, index, event)
          })
          $picker.addEventListener('touchmove', event => {
            event.preventDefault()
            event.stopPropagation()
            this.handleTouchMove($picker, index, event)
          })
          $picker.addEventListener('touchend', event => {
            this.handleTouchEnd($picker, index, event)
          })
        })
      },
      handleTouchStart ($target, index, event) {
        let currentPickerList = this.pickerListHandlers[index]
        currentPickerList.startY = event.touches[0].pageY
      },
      handleTouchMove ($target, index, event) {
        let currentPickerList = this.pickerListHandlers[index]
        currentPickerList.currentY = event.touches[0].pageY

        if (currentPickerList.currentY < currentPickerList.startY) { // 向上滑动
          if (currentPickerList.activeIndex === this.slots[index].values.length - 1) {
            currentPickerList.translateY += ((currentPickerList.currentY - currentPickerList.startY) / 80)
          } else {
            currentPickerList.translateY += (currentPickerList.currentY - currentPickerList.startY) / 20 // 除以2是为了降低滚动灵敏度
            if (Math.abs(currentPickerList.translateY - currentPickerList.initTranslateY) > (currentPickerList.activeIndex + 1) * this.pickerItemHeight) { // 滑动距离超过阈值，激活下一个item
              currentPickerList.activeIndex ++
            } else { // 滑动距离小于阈值，激活当前item
            }
          }
        } else { // 向下滑动
          if (currentPickerList.activeIndex === 0) { // 当前为第一个，继续向下时，降低滚动灵敏度 100倍
            currentPickerList.translateY += ((currentPickerList.currentY - currentPickerList.startY) / 80)
          } else {
            currentPickerList.translateY += (currentPickerList.currentY - currentPickerList.startY) / 20 // 除以2是为了降低滚动灵敏度
            if (Math.abs(currentPickerList.translateY - currentPickerList.initTranslateY) < (currentPickerList.activeIndex === 1 ? 1 : currentPickerList.activeIndex - 1) * this.pickerItemHeight) { // 滑动距离超过阈值，激活上一个item
              currentPickerList.activeIndex --
            } else { // 滑动距离小于阈值，激活当前item
            }
          }
        }
        this.translateY($target, currentPickerList.translateY)
        this.sliding(index)
      },
      handleTouchEnd ($target, index, event) {
        let currentPickerList = this.pickerListHandlers[index]
        currentPickerList.endY = event.changedTouches[0].pageY

        let itemLength = this.slots[index].values.length
        if (currentPickerList.endY < currentPickerList.startY) { // 向上滑动
          currentPickerList.activeIndex = currentPickerList.activeIndex > itemLength - 1 ? itemLength - 1 : currentPickerList.activeIndex

          currentPickerList.translateY = currentPickerList.initTranslateY - currentPickerList.activeIndex * this.pickerItemHeight
        } else { // 向下滑动
          currentPickerList.activeIndex = currentPickerList.activeIndex <= 0 ? 0 : currentPickerList.activeIndex
          currentPickerList.translateY = currentPickerList.initTranslateY - currentPickerList.activeIndex * this.pickerItemHeight
        }

        // 当前为第一个，松手后缓慢复位；当前为最后一个，松手后缓慢复位；
        let duration = currentPickerList.activeIndex === 0 || currentPickerList.activeIndex === itemLength - 1 ? 0.5 : 0.1
        this.translateY($target, currentPickerList.translateY, duration, 'ease-out')
        this.sliding(index, true)
      },
      translateY ($target, translateY, duration, timing = '') {
        $target.style.transform = `translate3d(0, ${translateY}px, 0)`
        if (duration) {
          $target.style.transition = `transform ${duration}s ${timing}`
        }
      },
      cancel (event) {
        this.$emit('cancel', event)
        this.close()
      },
      confirm () {
        this.$emit('confirm', this.getCheckedValues())
        this.close()
      },
      sliding (slotIndex, isTouchEnd) {
        this.$emit('sliding', this, slotIndex, isTouchEnd)
      },
      setSlotCheckedIndex (slotIndex, activeIndex) {
        let currentPickerList = this.pickerListHandlers[slotIndex]
        currentPickerList.activeIndex = activeIndex

        currentPickerList.translateY = currentPickerList.initTranslateY - currentPickerList.activeIndex * this.pickerItemHeight
        this.translateY(document.querySelectorAll('.sv-picker-list')[slotIndex], currentPickerList.translateY, 0.3)
      },
      getSlotCheckedIndex (slotIndex) {
        return this.pickerListHandlers[slotIndex].activeIndex
      },
      setCheckedIndexes (indexes) {
      },
      getCheckedIndexes () {
      },
      setSlotCheckedValue (slotIndex, value) {
        this.setSlotCheckedIndex(slotIndex, this.slots[slotIndex].values.indexOf(value))
      },
      getSlotCheckedValue (slotIndex) {
        return this.slots[slotIndex].values[this.pickerListHandlers[slotIndex].activeIndex]
      },
      setSlotValues (slotIndex, values) {
        this.computeSlots[slotIndex].values = values
      },
      getSlotValues (slotIndex) {

      },
      setCheckedValues (values) {
        values.forEach((value, index) => {
          this.setSlotCheckedValue(index, value)
        })
      },
      getCheckedValues () {
        let values = []

        for (let i = 0; i < this.slots.length; i++) {
          values.push(this.slots[i].values[this.pickerListHandlers[i].activeIndex])
        }
        return values
      },
      open () {
        this.visible = true
        this.$nextTick(() => {
          this.computeSomeValue()
          this.bindEvents()
        })
      },
      close () {
        this.visible = false
        this.reset()
      },
      reset () {
        this.pickerListHandlers = []

        for (let i = 0, len = this.slots.length; i < len; i++) {
          this.pickerListHandlers.push({
            activeIndex: 0,
            scrollHeight: 0,
            startY: 0,
            endY: 0,
            currentY: 0,
            initTranslateY: 0,
            translateY: 0
          })

          this.slots[i].values = []
        }
      }
    },
    mounted () {
      this.initData()
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";
  .sv-picker{
    .sv-picker-header{
      display: flex;
      justify-content: space-between;
    }
    .sv-picker-body{
      position: relative;
      font-size: pxToRem(28);
      background: #f2f2f2;
      .sv-picker-handle{
        position: absolute;
        top:50%;
        transform: translateY(-50%);
        width: 100%;
        height: pxToRem(60);
        line-height: pxToRem(60);
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
      }
    }
  }
</style>



