<template>
  <section class="sv-route-wrapper" :class="['sv-route-size-' + size]">
    <div class="route-name">{{from}}</div>
    <i class="route-icon icon-route-connect"></i>
    <div class="route-name">{{to}}</div>
  </section>
</template>

<script>
  export default {
    name: 'svRoute',
    props: {
      from: {
        type: [String],
        default: ''
      },
      to: {
        type: [String],
        default: ''
      },
      size: {
        type: String,
        default: 'normal'
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";
  .sv-route-wrapper{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    .route-name{
      max-width: 46%;
      word-break: break-all;
    }
    .route-icon{
      align-self: center;
      width: 8%;
      text-align: center;
      color: #333;
    }
    &.sv-route-size-normal{
      .route-name{
      }
    }
    &.sv-route-size-large{
      .route-name{
      }
    }
  }
</style>



