<template>
  <figure class="sv-grid-cell">
    <div class="cell-icon" :class="cellIconClass" :style="cellIconStyle">
      <slot name="icon"></slot>
    </div>
    <figcaption class="cell-text">
      <slot></slot>
    </figcaption>
  </figure>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'svGridCell',
    props: {
      icon: String
    },
    data () {
      return {
        cellIconStyle: {
          display: 'block'
        }
      }
    },
    computed: {
      cellIconClass () {
        return this.icon ? `sv-icon-${this.icon}` : ''
      }
    },
    mounted () {
      if (!this.$el.children[0].innerHTML && !this.icon) {
        this.cellIconStyle.display = 'none'
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
</style>



