<template>
  <div class="sv-radio" @click="handleClick">
    <i class="sv-radio-icon" :class="{'active': radioGroupValue === value}"></i>
    <span class="sv-radio-label">{{label}}</span>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'svRadio',
    data () {
      return {
        radioGroupValue: ''
      }
    },
    props: {
      label: String,
      value: [String, Number, Boolean]
    },
    methods: {
      handleClick () {
        this.$parent.radioGroupValue = this.value
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";
  .sv-radio{
    display: inline-flex;
    white-space: nowrap;
    align-items: center;
    font-size: 0;
    line-height: 1;
    .sv-radio-icon{
      position: relative;
      width: pxToRem(32);
      height: pxToRem(32);
      border-radius: pxToRem(16);
      border: pxToRem(2) solid $color-success;
      &.active{
        &::after{
          content: '';
          position: absolute;
          top:50%;
          left:50%;
          transform: translate(-50%, -50%);
          width: pxToRem(18);
          height: pxToRem(18);
          border-radius: pxToRem(9);
          background: $color-success;
        }
      }
    }
    .sv-radio-label{
      flex: 1;
      margin-left: pxToRem(4);
      font-size: pxToRem(28);
      color:#333;
    }
  }
</style>



