import Dialog from './Dialog'

const defaultOptions = {
  title: '提示',
  message: '',
  type: 'alert',
  cancelButtonText: '取消',
  confirmButtonText: '确定'
}

let DialogConstructor
let instance
let createInstance = () => {
  if (!instance) {
    instance = new DialogConstructor().$mount(document.createElement('div'))
  }
  return instance
}

export default {
  install (Vue) {
    DialogConstructor = Vue.extend(Dialog)

    let $dialog = (options, title) => {
      if (typeof options === 'string') {
        options = {
          message: options,
          title
        }
      }

      options = Object.assign(defaultOptions, options)

      let instance = createInstance()
      instance._setOptions(options)
      document.body.appendChild(instance.$el)
      instance._open()
      return new Promise((resolve, reject) => {
        instance.confirmCallback = resolve
        instance.cancelCallback = reject
      })
    }

    DialogConstructor.prototype._setOptions = (options) => {
      for (let prop in options) {
        if (options.hasOwnProperty(prop)) {
          instance[prop] = options[prop]
        }
      }
    }

    $dialog.alert = (message, title = '提示', options) => {
      if (typeof title === 'object') {
        options = title
        title = ''
      }
      return $dialog(Object.assign({
        type: 'alert',
        title,
        message
      }, options))
    }

    $dialog.confirm = (message, title, options) => {
      if (typeof title === 'object') {
        options = title
        title = ''
      }
      return $dialog(Object.assign({
        type: 'confirm',
        title,
        message
      }, options))
    }

    $dialog.prompt = (title = '请输入', options) => {
      if (typeof title === 'object') {
        options = title
        title = '请输入'
      }
      return $dialog(Object.assign({
        type: 'prompt',
        title
      }, options))
    }
    Vue.prototype.$dialog = $dialog
  }
}



