<template>
  <header class="sv-header" :class="[{'is-fixed': fixed}, headerColorClass]">
    <div class="sv-header-left">
      <i class="back-icon sv-icon-arrow-left" @click="goBack" v-if="(typeof hasBack === 'string' && hasBack === 'true') || (typeof hasBack === 'boolean' && hasBack)"></i>
      <slot name="left"></slot>
    </div>
    <div class="sv-header-title">
      <slot>{{title}}</slot>
    </div>
    <div class="sv-header-right">
      <slot name="right"></slot>
    </div>
  </header>
</template>

<script>
  export default {
    name: 'svHeader',
    props: {
      title: {
        type: String
      },
      fixed: {
        type: Boolean,
        default: true
      },
      hasBack: {
        type: [Boolean, String],
        default: false
      },
      color: {
        type: String,
        default: 'light'
      }
    },
    computed: {
      headerColorClass () {
        return `sv-header-${this.color}`
      }
    },
    methods: {
      goBack () {
        window.history.back()
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../../styles/base";
  @import "../../../icon-fonts/style.css";
  .sv-header{
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding: pxToRem(0 20);
    height: pxToRem(100px);
    line-height: 1;
    text-align: center;
    background: #fff;
    font-size: pxToRem(26);
    &.is-fixed{
      position: fixed;
      top:0;
      left:0;
      right:0;
      z-index:1;
      border-bottom: pxToRem(1px) solid #bfbfbf;
    }
    .sv-header-left{
      flex: .5;
      text-align: left;
      .back-icon{
        font-size: pxToRem(38);
        color:$color-light;
      }
    }
    .sv-header-title{
      flex: 3;
      font-size: pxToRem(36px);
      color: $color-light;
      .sv-input-wrapper{
        height: pxToRem(60);
        border: 1px solid #999;
        border-radius: pxToRem(30);
        font-size: pxToRem(26px);
        .sv-input-icon{
          width: auto;
          line-height: pxToRem(60);
          background: none;
          border-left:none;
          color:#aaa;
          font-size: pxToRem(32);
        }
        .sv-input{
          height: 100%;
          padding-right: pxToRem(20);
          background: transparent;
          outline: none;
          border:none;
          font-size: pxToRem(26px);
        }
      }
    }
    .sv-header-right{
      flex: .5;
      .sv-button{
        padding: 0;
      }
    }
    &.sv-header-light{
      background: $color-light;
      .sv-header-left{
        .back-icon{
          color:#999;
        }
      }
      .sv-header-title{
        color:#333;
      }
    }
    &.sv-header-primary{
      background: $color-primary;
    }
    &.sv-header-success{
      background: $color-success;
    }
  }
</style>



