<template>
  <!--<li class="sv-slide-item" :style="slideItemStyle" @touchstart="slideItemIndex = item">-->
  <li class="sv-slide-item" :style="slideItemStyle">
    <slot></slot>
  </li>
</template>

<script type="text/ecmascript-6">
  import '../../scripts/calcHtmlFontSize.js'
  export default {
    name: 'svSlideItem',
    data () {
      return {
        slideItemStyle: {
          height: document.body.clientHeight + 2 + 'px'
        }
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
</style>



