<template>
  <div class="sv-loadmore">
    <div class="sv-loadmore-content" :class="{'is-dropped': topDropped || bottomDropped}" :style="{ 'transform': 'translate3d(0, ' + translate + 'px, 0)' }">
      <slot name="top">
        <div class="sv-loadmore-top" v-if="">
          <div class="sv-loadmore-spinner" :class="{'rotate': topStatus === 'drop'}">
            <template v-if="topStatus !== 'loading'">↓</template>
            <template v-else>
              <div class="loading3">
                <div></div>
                <div></div>
              </div>
            </template>
          </div>
          <span class="sv-loadmore-text">{{ topText }}</span>
        </div>
      </slot>
      <slot></slot>
      <div class="sv-loadmore-bottom-allloaded-text" v-if="bottomAllLoaded">{{bottomAllLoadedText}}</div>
      <slot name="bottom">
        <div class="sv-loadmore-bottom" v-if="bottomShow">
          <div class="sv-loadmore-spinner" :class="{'rotate': bottomStatus === 'pull'}">
            <template v-if="bottomStatus !== 'loading'">↓</template>
            <template v-else>
              <div class="loading3">
                <div></div>
                <div></div>
              </div>
            </template>
          </div>
          <span class="sv-loadmore-text">{{bottomText}}</span>
        </div>
      </slot>
    </div>
  </div>
</template>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";

  .sv-loadmore {
    min-height: 100%;
    .sv-loadmore-content {
      &.is-dropped {
        transition: .2s;
      }
      .sv-loadmore-bottom-allloaded-text{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: pxToRem(10px 0);
        font-size: pxToRem(28px);
        text-align: center;
        color: #999;
        &:before, &:after{
          content: '';
          display: inline-block;
          width: pxToRem(100px);
          height: 0;
          border:1px solid #999;
        }
        &:before{
          margin-right: pxToRem(10px);
        }
        &:after{
          margin-left: pxToRem(10px);
        }
      }
      .sv-loadmore-top, .sv-loadmore-bottom {
        color: #999;
        font-size: pxToRem(32px);
        text-align: center;
        height: pxToRem(50px);
        line-height: pxToRem(50px);
      }
      .sv-loadmore-top {
        margin-top: pxToRem(-50px);
      }
      .sv-loadmore-bottom {
        margin-bottom: pxToRem(-50px);
      }
      .sv-loadmore-spinner {
        display: inline-block;
        margin-right: pxToRem(5px);
        vertical-align: middle;
        &.rotate{
          transform: rotate(180deg);
        }
        .loading3 {
          border-radius:50%;
          height: pxToRem(30px);
          position:relative;
          width:pxToRem(30px);
          /* css3 keyframes - animation 3 */
          @-webkit-keyframes anim3 {
            from { -webkit-transform:rotateY(0deg); }
            50% { -webkit-transform:rotateY(180deg); }
            to { -webkit-transform:rotateY(360deg); }
          }
          @-moz-keyframes anim3 {
            from { -moz-transform:rotateY(0deg); }
            50% { -moz-transform:rotateY(180deg); }
            to { -moz-transform:rotateY(360deg); }
          }
          @-ms-keyframes anim3 {
            from { -ms-transform:rotateY(0deg); }
            50% { -ms-transform:rotateY(180deg); }
            to { -ms-transform:rotateY(360deg); }
          }
          @-o-keyframes anim3 {
            from { -o-transform:rotateY(0deg); }
            50% { -o-transform:rotateY(180deg); }
            to { -o-transform:rotateY(360deg); }
          }
          @keyframes anim3 {
            from { transform:rotateY(0deg); }
            50% { transform:rotateY(180deg); }
            to { transform:rotateY(360deg); }
          }
          div:nth-child(1), div:nth-child(2){
            background-color:rgba(255, 255, 255, 0.2);
            border:pxToRem(5px) solid #FFF;
            border-radius:50%;
            box-shadow:0 0 pxToRem(5px) 0 $color-danger;
            height:pxToRem(30px);
            position:absolute;
            top:0;

            /* set top and bottom border widths to 0 */
            border-top-width:0;
            border-bottom-width:0;

            animation:anim3 2s linear infinite;
          }
          div:nth-child(1) {
            border-color:$color-danger;
            left:0;
            width:pxToRem(20px);
          }
          div:nth-child(2) {
            border-color:$color-danger;
            left:pxToRem(-5px);
            width:pxToRem(30px);
            animation-delay:0.5s;
          }
        }
      }
      .sv-loadmore-text {

        vertical-align: middle;
      }
    }
  }
</style>

<script type="text/ecmascript-6">
  export default {
    name: 'svLoadmore',
    props: {
      maxDistance: {
        type: Number,
        default: 0
      },
      autoFill: {
        type: Boolean,
        default: true
      },
      distanceIndex: {
        type: Number,
        default: 2
      },
      topPullText: {
        type: String,
        default: '下拉刷新'
      },
      topDropText: {
        type: String,
        default: '释放刷新'
      },
      topLoadingText: {
        type: String,
        default: '加载中...'
      },
      topDistance: {
        type: Number,
        default: 70
      },
      topMethod: {
        type: Function
      },
      bottomPullText: {
        type: String,
        default: '上拉加载更多'
      },
      bottomDropText: {
        type: String,
        default: '释放加载更多'
      },
      bottomLoadingText: {
        type: String,
        default: '加载中...'
      },
      bottomDistance: {
        type: Number,
        default: 70
      },
      bottomMethod: {
        type: Function
      },
      bottomAllLoaded: {
        type: Boolean,
        default: false
      },
      bottomAllLoadedText: {
        type: String,
        default: '别拉啦，真的没有啦'
      }
    },
    data () {
      return {
        translate: 0,
        scrollEventTarget: null,
        containerFilled: false,
        topText: '',
        topDropped: false,
        bottomText: '',
        bottomDropped: false,
        bottomReached: false,
        direction: '',
        startY: 0,
        startScrollTop: 0,
        currentY: 0,
        topStatus: '',
        bottomStatus: '',
        topShow: false,
        bottomShow: false
      }
    },
    watch: {
      translate (val) {
        this.topShow = val > 0 && this.topMethod
        this.bottomShow = val < 0 && this.bottomMethod && !this.bottomAllLoaded
      },
      topStatus (val) {
        this.$emit('top-status-change', val)
        switch (val) {
          case 'pull':
            this.topText = this.topPullText
            break
          case 'drop':
            this.topText = this.topDropText
            break
          case 'loading':
            this.topText = this.topLoadingText
            break
        }
      },
      bottomStatus (val) {
        this.$emit('bottom-status-change', val)
        switch (val) {
          case 'pull':
            this.bottomText = this.bottomPullText
            break
          case 'drop':
            this.bottomText = this.bottomDropText
            break
          case 'loading':
            this.bottomText = this.bottomLoadingText
            break
        }
      }
    },
    methods: {
      onTopLoaded () {
        this.translate = 0
        setTimeout(() => {
          this.topStatus = 'pull'
        }, 200)
      },
      onBottomLoaded () {
        this.bottomStatus = 'pull'
        this.bottomDropped = false
        this.$nextTick(() => {
          if (this.scrollEventTarget === window) {
            document.body.scrollTop += 50
          } else {
            this.scrollEventTarget.scrollTop += 50
          }
          this.translate = 0
        })
        if (!this.bottomAllLoaded && !this.containerFilled) {
          this.fillContainer()
        }
      },
      getScrollEventTarget (element) {
        let currentNode = element
        while (currentNode && currentNode.tagName !== 'HTML' &&
        currentNode.tagName !== 'BODY' && currentNode.nodeType === 1) {
          let overflowY = document.defaultView.getComputedStyle(currentNode).overflowY
          if (overflowY === 'scroll' || overflowY === 'auto') {
            return currentNode
          }
          currentNode = currentNode.parentNode
        }
        return window
      },
      getScrollTop (element) {
        if (element === window) {
          return Math.max(window.pageYOffset || 0, document.documentElement.scrollTop)
        } else {
          return element.scrollTop
        }
      },
      bindTouchEvents () {
        this.$el.addEventListener('touchstart', this.handleTouchStart)
        this.$el.addEventListener('touchmove', this.handleTouchMove)
        this.$el.addEventListener('touchend', this.handleTouchEnd)
      },
      init () {
        this.topStatus = 'pull'
        this.bottomStatus = 'pull'
        this.topText = this.topPullText
        this.scrollEventTarget = this.getScrollEventTarget(this.$el)
        if (typeof this.bottomMethod === 'function') {
          this.fillContainer()
          this.bindTouchEvents()
        }
        if (typeof this.topMethod === 'function') {
          this.bindTouchEvents()
        }
      },
      fillContainer () {
        if (this.autoFill) {
          this.$nextTick(() => {
            if (this.scrollEventTarget === window) {
              this.containerFilled = this.$el.getBoundingClientRect().bottom >=
                document.documentElement.getBoundingClientRect().bottom
            } else {
              this.containerFilled = this.$el.getBoundingClientRect().bottom >=
                this.scrollEventTarget.getBoundingClientRect().bottom
            }
            if (!this.containerFilled) {
              this.bottomStatus = 'loading'
              this.bottomMethod()
            }
          })
        }
      },
      checkBottomReached () {
        if (this.scrollEventTarget === window) {
          return document.body.scrollTop + document.documentElement.clientHeight >= document.body.scrollHeight
        } else {
          return this.$el.getBoundingClientRect().bottom <= this.scrollEventTarget.getBoundingClientRect().bottom + 1
        }
      },
      handleTouchStart (event) {
        this.startY = event.touches[0].clientY
        this.startScrollTop = this.getScrollTop(this.scrollEventTarget)
        this.bottomReached = false
        if (this.topStatus !== 'loading') {
          this.topStatus = 'pull'
          this.topDropped = false
        }
        if (this.bottomStatus !== 'loading') {
          this.bottomStatus = 'pull'
          this.bottomDropped = false
        }
      },
      handleTouchMove (event) {
        if (this.startY < this.$el.getBoundingClientRect().top && this.startY > this.$el.getBoundingClientRect().bottom) {
          return
        }
        this.currentY = event.touches[0].clientY
        let distance = (this.currentY - this.startY) / this.distanceIndex
        this.direction = distance > 0 ? 'down' : 'up'
        if (typeof this.topMethod === 'function' && this.direction === 'down' &&
          this.getScrollTop(this.scrollEventTarget) === 0 && this.topStatus !== 'loading') {
          event.preventDefault()
          event.stopPropagation()
          if (this.maxDistance > 0) {
            this.translate = distance <= this.maxDistance ? distance - this.startScrollTop : this.translate
          } else {
            this.translate = distance - this.startScrollTop
          }
          if (this.translate < 0) {
            this.translate = 0
          }
          this.topStatus = this.translate >= this.topDistance ? 'drop' : 'pull'
        }
        if (this.direction === 'up') {
          this.bottomReached = this.bottomReached || this.checkBottomReached()
        }
        if (typeof this.bottomMethod === 'function' && this.direction === 'up' &&
          this.bottomReached && this.bottomStatus !== 'loading' && !this.bottomAllLoaded) {
          event.preventDefault()
          event.stopPropagation()
          if (this.maxDistance > 0) {
            this.translate = Math.abs(distance) <= this.maxDistance
              ? this.getScrollTop(this.scrollEventTarget) - this.startScrollTop + distance : this.translate
          } else {
            this.translate = this.getScrollTop(this.scrollEventTarget) - this.startScrollTop + distance
          }
          if (this.translate > 0) {
            this.translate = 0
          }
          this.bottomStatus = -this.translate >= this.bottomDistance ? 'drop' : 'pull'
        }
        this.$emit('translate-change', this.translate)
      },
      handleTouchEnd () {
        if (this.direction === 'down' && this.getScrollTop(this.scrollEventTarget) === 0 && this.translate > 0) {
          this.topDropped = true
          if (this.topStatus === 'drop') {
            this.translate = '50'
            this.topStatus = 'loading'
            this.topMethod()
          } else {
            this.translate = '0'
            this.topStatus = 'pull'
          }
        }
        if (this.direction === 'up' && this.bottomReached && this.translate < 0) {
          this.bottomDropped = true
          this.bottomReached = false
          if (this.bottomStatus === 'drop') {
            this.translate = '-50'
            this.bottomStatus = 'loading'
            this.bottomMethod()
          } else {
            this.translate = '0'
            this.bottomStatus = 'pull'
          }
        }
        this.$emit('translate-change', this.translate)
        this.direction = ''
      }
    },
    mounted () {
      this.init()
    }
  }
</script>



