<template>
  <div class="sv-popup" :class="[popupClass]" v-show="visible" :style="popupStyle">
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'svPopup',
    data () {
      return {
        visible: false,
        closing: false
      }
    },
    props: {
      value: {
        type: Boolean,
        default: false
      },
      position: {
        type: String,
        default: 'center'
      },
      top: {
        type: [String, Number],
        default: 0
      },
      maxHeight: [String, Number]
    },
    computed: {
      popupClass () {
        let className = `sv-popup-${this.position}`

        if (this.closing) {
          className += ' closing'
        }
        return className
      },
      popupStyle () {
        let style = {}
        if (this.top) {
          style.top = this.convertToPx(this.top)
        }
        if (this.maxHeight) {
          style.maxHeight = this.convertToPx(this.maxHeight)
        }
        return style
      }
    },
    watch: {
      value (val) {
        if (val) {
          this.visible = true
        } else {
          this.close()
        }
      },
      visible (val) {
        this.$emit('input', val)

        if (val) {
          this.closing = false
          this.$mask.open()
          this.$mask.getZIndex().then(maskZIndex => {
            this.$el.style.zIndex = maskZIndex + 1
          })
        } else {
          this.$mask.close()
        }
      }
    },
    methods: {
      convertToPx (unit) {
        let clientHeight = document.body.clientHeight

        if (typeof unit === 'string') {
          if (unit.match('%')) {
            return `${(clientHeight * (window.parseFloat(unit.substring(0, unit.length - 1)) / 100)).toFixed(2)}px`
          } else if (/^[0-9]+$/.test(unit)) {
            return `${unit}px`
          } else {
            return null
          }
        } else if (typeof unit === 'number') {
          return `${unit}px`
        } else {
          return null
        }
      },
      close () {
        this.closing = true
        this.$emit('close')
        window.setTimeout(() => {
          this.visible = false
//          this.$emit('close')
        }, 200)
      }
    },
    mounted () {
      if (this.visible) {
        this.$mask.open()
      }
      this.$mask.$on('close', () => {
        this.close()
      })
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../styles/base";
  .sv-popup{
    position: fixed;
    background: $color-light;
    overflow-y: auto;
    &.sv-popup-center{
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      /*border-radius: pxToRem(20);*/
    }
    &.sv-popup-bottom{
      left:0;
      right:0;
      bottom:0;
      animation: slow-open .2s ease-in;
      &.closing{
        animation: slow-close .2s ease-in;
      }
      @keyframes slow-open {
        0%{
          transform: scaleY(0);
          transform-origin: left bottom;
        }
        100%{
          transform: scaleY(1);
          transform-origin: left bottom;
        }
      }
      @keyframes slow-close {
        0%{
          transform: scaleY(1);
          transform-origin: left bottom;
        }
        100%{
          transform: scaleY(0);
          transform-origin: left bottom;
        }
      }
    }
  }
</style>



